.nav {
   grid-area: nav;
   background-color: var(--main-black);
   padding: 40px 20px 0 20px;
 }
.logo {
  width: 60px;
  background-color:aliceblue;
  border-radius: 30%;
}
.nav_links {
  padding: 40px 0 0 0;
}
.nav_link{
  padding-bottom: 20px;
}
.nav_link::after {
  display:block;
  content:'';
  height: 1px;
  background-color: var(--main-white);
  z-index: 1;
  margin: 0 auto;
}
 .item {
   color: var(--main-white);
   text-decoration: none;
 }
.active {
  color: var(--main-white);
  text-decoration: none;
  font-weight: 600;

}
